<template>
  <div>
    <Transition>
      <div class="alert-wrap">
        <v-alert
          v-if="showPost"
          dismissible
          prominent
          type="success"
        >
          <b>{{ motivationalQuotes[randomQuote] }}</b> Pedido
          cadastrado com sucesso!
        </v-alert>

        <v-alert
          v-if="showUpdate"
          dismissible
          prominent
          type="success"
        >
          <b>{{ motivationalQuotes[randomQuote] }}</b> Pedido
          atualizado com sucesso!
        </v-alert>

        <v-alert
          v-if="showError"
          dismissible
          prominent
          type="error"
        >
          <b>Erro desconhecido!</b> A página será atualizada.
        </v-alert>
      </div>
    </Transition>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: "AlertBanners",

  data: () => ({
    motivationalQuotes: [
      "Nunca desista!",
      "Você está cada vez melhor!",
      "1% ao dia, todo dia.",
      "Você consegue!.",
      "Seu futuro é brilhante!",
    ],
  }),

  computed: {
    // (folder, [states])
    ...mapState("financial", ["showPost", "showUpdate", "showError"]),
    randomQuote () {
      return Math.floor(Math.random() * this.motivationalQuotes.length)
    },
  },
}
</script>

<style>
.alert-wrap {
    position: absolute;
    z-index: 10
}

.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
  opacity: 0;
}
</style>