<template>
  <div>
    <h1>Controle Financeiro - Remake</h1>
    <v-btn @click="handleClick">Log</v-btn>
    <AlertBanners/>
    <FeeTable />
  </div>

</template>

<script>
import AlertBanners from "@/components/app/AlertBanners.vue"
import FeeTable from "@/components/tables/FeeTable.vue"

export default {
    name: "FinancialControlRemake",
    components: {
        AlertBanners,
        FeeTable
    },
    computed: {
        loggedIn () {
            return window.sessionStorage.logado == "true" ? true : false
        },
    },
    created() {
        this.loginValidation();
    },
    methods: {
        loginValidation () {
            if (this.loggedIn != true) {
                return window.location.href = "LoginScreen"
            }
            else {
                return
            }
        },
        handleClick () {
            // ("folder/mutation")
            this.$store.commit("financial/log")
        }
    },
}
</script>